a {
  text-decoration: none;
  color: #E0ECFF;
}
.team-info .team-bg-box {
  width: 100%;
  padding-bottom: 150px;
  background: url('./img/team-bg.jpg');
  background-size: 100% 100%;
  background-repeat: no-repeat;
}
.team-info .team-bg-box .team-title {
  text-align: center;
  padding-top: 63px;
  color: #FFFFFF;
  font-size: 48px;
  font-style: normal;
  font-weight: normal;
}
.team-info .team-bg-box .team-describe {
  text-align: center;
  font-size: 20px;
  margin: 0 auto;
  margin-top: 60px;
  padding: 0 120px;
  font-weight: normal;
}
.team-info .team-bg-box .hr {
  height: 75px;
  margin-top: 74px;
  position: relative;
}
.team-info .team-bg-box .hr .down {
  position: absolute;
  width: 64px;
  height: 66px;
  text-align: center;
  line-height: 66px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.team-info .team-bg-box .hr .long {
  margin: 0 auto;
  width: 1180px;
  height: 1px;
  background: linear-gradient(to right, rgba(255, 255, 255, 0.5), rgba(255, 0, 0, 0));
}
.team-info .team-bg-box .hr .short {
  margin: 0 auto;
  width: 336px;
  height: 1px;
  margin-top: 50px;
  background: linear-gradient(to right, rgba(255, 0, 0, 0), rgba(255, 255, 255, 0.5));
}
.team-info .team-bg-box .hr .middle {
  margin: 0 auto;
  width: 666px;
  height: 1px;
  margin-top: 25px;
  background: linear-gradient(to right, rgba(255, 255, 255, 0.4), rgba(255, 0, 0, 0));
}
.team-info .team-bg-box .team-box-figure-info {
  width: 1334px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}
.team-info .team-bg-box .team-box-figure-info .figure-info {
  width: 632px;
  height: 540px;
  position: relative;
  display: flex;
  align-items: flex-end;
  margin-top: 80px;
}
.team-info .team-bg-box .team-box-figure-info .figure-info .figure-describe {
  width: 100%;
  height: 440px;
  background: linear-gradient(0deg, rgba(255, 255, 255, 0.03), rgba(255, 255, 255, 0.03)), linear-gradient(61.79deg, rgba(205, 122, 55, 0.07) 7.46%, rgba(130, 71, 229, 0.07) 69.49%);
  backdrop-filter: blur(6px);
  border: 1px solid;
  border-image: linear-gradient(to right, #8247E5, #CD7A37) 1;
  display: flex;
  align-items: center;
  justify-content: center;
}
.team-info .team-bg-box .team-box-figure-info .figure-info .figure-describe .figure-describe-box .describe-box {
  text-align: center;
  padding: 0 22px;
  margin-top: 20px;
  color: #FFFFFF;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
}
.team-info .team-bg-box .team-box-figure-info .figure-info .figure-describe .figure-describe-box img {
  margin-left: 39px;
  margin-top: 32px;
}
.team-info .team-bg-box .team-box-figure-info .figure-info .figure-describe .figure-describe-box .name-job {
  text-align: center;
  margin-top: 24px;
}
.team-info .team-bg-box .team-box-figure-info .figure-info .figure-describe .figure-describe-box .name-job .name {
  font-size: 24px;
  color: #2564F4;
  font-weight: bold;
}
.team-info .team-bg-box .team-box-figure-info .figure-info .figure-describe .figure-describe-box .name-job .job {
  font-size: 16px;
  font-weight: normal;
}
.team-info .team-bg-box .team-box-figure-info .figure-info .figure-logo {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  position: absolute;
  left: 50%;
  top: 0;
  z-index: 1;
  transform: translateX(-50%);
}
.team-info .team-bg-box .team-box-figure-info .figure-info .ceo {
  background: url('./img/ceo.svg');
  background-size: 100%;
}
.team-info .team-bg-box .team-box-figure-info .figure-info .cto {
  background: url('./img/richard.jpg');
  background-size: 100%;
}
.team-info .team-bg-box .team-box-figure-info .figure-info .jason {
  background: url('./img/jason.svg');
  background-size: 100%;
}
.team-info .team-bg-box .team-box-figure-info .figure-info .janice {
  background: url('./img/janice.jpg');
  background-size: 100%;
}
.team-info .team-bg-box .team-box-figure-info .figure-info .daniel {
  background: url('./img/daniel.jpg');
  background-size: 100%;
}
@media only screen and (min-width: 1042px) and (max-width: 1440px) {
  .team-info .team-bg-box .hr {
    height: 72px;
  }
  .team-info .team-bg-box .hr .long {
    width: 864px;
  }
  .team-info .team-bg-box .hr .short {
    width: 259px;
  }
  .team-info .team-bg-box .hr .middle {
    width: 514px;
  }
  .team-info .team-bg-box .team-describe {
    padding: 0 60px;
  }
  .team-info .team-bg-box .team-box-figure-info {
    width: 1013px;
  }
  .team-info .team-bg-box .team-box-figure-info .figure-info {
    width: 488px;
    height: 548px;
  }
  .team-info .team-bg-box .team-box-figure-info .figure-info .figure-describe {
    height: 448px;
  }
}
@media only screen and (min-width: 660px) and (max-width: 1042px) {
  .team-info .team-bg-box .hr {
    height: 72px;
  }
  .team-info .team-bg-box .hr .long {
    width: 640px;
  }
  .team-info .team-bg-box .hr .short {
    width: 159px;
  }
  .team-info .team-bg-box .hr .middle {
    width: 314px;
  }
  .team-info .team-bg-box .team-describe {
    padding: 0 60px;
  }
  .team-info .team-bg-box .team-box-figure-info {
    width: 634px;
    display: block;
  }
}
@media only screen and (max-width: 659px) {
  .team-info .team-bg-box .hr {
    height: 72px;
  }
  .team-info .team-bg-box .hr .long {
    width: 325px;
  }
  .team-info .team-bg-box .hr .short {
    width: 131px;
  }
  .team-info .team-bg-box .hr .middle {
    width: 259px;
  }
  .team-info .team-bg-box .team-title {
    min-width: 375px;
  }
  .team-info .team-bg-box .team-describe {
    padding: 0 24px;
    min-width: 375px;
    text-align: left;
  }
  .team-info .team-bg-box .team-box-figure-info {
    width: 328px;
    display: block;
  }
  .team-info .team-bg-box .team-box-figure-info .figure-info {
    width: 328px;
    height: 587px;
  }
  .team-info .team-bg-box .team-box-figure-info .figure-info .figure-logo {
    width: 135px;
    height: 135px;
  }
  .team-info .team-bg-box .team-box-figure-info .figure-info .figure-describe {
    height: 520px;
  }
}
